import React from "react";
import { Link, graphql } from "gatsby";
import { rhythm, scale } from "../utils/typography";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTag } from '@fortawesome/free-solid-svg-icons';

import Bio from "../components/bio";
import Layout from "../components/layout";
import Post from "../components/post";
import SEO from "../components/seo";

const TagPageTemplate = ({ data, pageContext, location }) => {
  const { tag, index, } = pageContext;
  const siteTitle = data.site.siteMetadata.title;
  const allPosts = data.allMarkdownRemark.edges;
  const numPosts = data.site.siteMetadata.settings.postsPerOnePage;
  const startIndex = (index - 1) * numPosts;
  const endIndex = index * numPosts > allPosts ? allPosts : index * numPosts;
  const allTagPosts = allPosts.filter(post => post.node.frontmatter.tags.includes(tag));
  const posts = allTagPosts.slice(startIndex, endIndex);
  const maxIndex = Math.ceil(allTagPosts.length / numPosts);

  const navigation = (
    <nav>
      <ul
        style={{
          display: `flex`,
          flexWrap: `wrap`,
          justifyContent: `space-between`,
          listStyle: `none`,
          padding: 0,
        }}
      >
        <li>
          {index > 1 && (
            <Link to={`tags/${tag}/page${index - 1}`} rel="prev">
              ← New Posts
            </Link>
          )}
        </li>
        <li>
          {index !== maxIndex && (
            <Link to={`tags/${tag}/page${index + 1}`} rel="next">
              Old Posts →
            </Link>
          )}
        </li>
      </ul>
    </nav>
  );

  return (
    <Layout location={location} title={siteTitle}>
      <SEO title="All posts" />
      <Bio />
      <h2
        style={{
          marginTop: rhythm(1),
          marginBottom: rhythm(1),
        }}
      >
        <FontAwesomeIcon icon={faTag} style={{marginRight: "10px"}}/>
        Tag: {tag} ({allTagPosts.length}posts)
      </h2>
      {navigation}
      {posts.map(({ node }) => <Post {...node} key={node.fields.slug} />)}
      {navigation}
    </Layout>
  );
};

export default TagPageTemplate;

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
        settings {
          postsPerOnePage
        }
      }
    }
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      edges {
        node {
          excerpt
          fields {
            slug
          }
          frontmatter {
            date(formatString: "MM/DD YYYY")
            title
            description
            tags
          }
        }
      }
    }
  }
`;
